html {
  font-size: 16px;
  font-family: "Slabo 13px", serif, sans-serif;
}

img {
  width: 100%;
  height: auto;
}

h1, h2, h3, h4 {
  font-family: "Milonga", serif;
  margin: 0;
  margin-bottom: 1rem;
}

ul {
  padding-left: 2rem;
}

li {
  margin-bottom: 0.5rem;
}

header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #0e285d;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}
header h1 {
  text-align: center;
  color: #edf8ff;
  margin: 0;
  text-shadow: 0 0 1rem #121212;
}
header h1 small {
  font-family: "Slabo 13px", serif;
}
header .cover {
  background-image: url("../img/map.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 2rem;
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
  border: #CEAA24 solid 2px;
}
header .cover img {
  width: 150px;
  height: auto;
  margin-bottom: 1rem;
}
header .content {
  padding: 2rem;
}

.wrapper {
  background-color: #071531;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  position: relative;
  overflow-y: scroll;
}
.wrapper main {
  height: 100%;
  align-self: stretch;
}

footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #0e285d;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}
footer .cover {
  background-image: url("../img/map.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 2rem;
  border-top-left-radius: 2rem;
  border-top-right-radius: 2rem;
  border: #CEAA24 solid 2px;
}
footer .cover img {
  width: 150px;
  height: auto;
  margin-bottom: 1rem;
}
footer .attribution {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-self: flex-end;
  width: 100%;
  font-size: 12px;
  text-align: center;
  color: #edf8ff;
  text-shadow: 0px 0px 0.5rem #121212;
}
footer .attribution a {
  color: #CEAA24;
  text-decoration: none;
  font-weight: bold;
}
footer .attribution a:hover {
  text-decoration: underline;
}

form .form-group {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin-bottom: 1rem;
}
form .form-group label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  border: 0;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  border-bottom: 0;
  background-color: #153b89;
  font-size: 14px;
}
form .form-group label span {
  margin-right: 12px;
}
form .form-group input, form .form-group textarea {
  padding: 0.6666666667rem 1rem;
  background-color: #0e285d;
  color: #edf8ff;
  border: none;
}
form .form-group input:focus, form .form-group textarea:focus {
  outline: 2px solid #edf8ff;
  background-color: #153b89;
}
form .btn-group {
  display: flex;
  flex-direction: column;
}
form .btn-group button {
  margin-bottom: 0.5rem;
  border: none;
}
form .btn-group button#submit {
  order: -1;
}
@media (min-width: 768px) {
  form .btn-group {
    flex-direction: row;
    justify-content: space-between;
    gap: 2rem;
  }
  form .btn-group button#submit {
    order: 0;
  }
}

@media (min-width: 768px) {
  .card-form {
    width: 70%;
    margin: auto;
  }
}

.container-form {
  padding: 0.5rem;
}
@media (min-width: 768px) {
  .container-form {
    padding: 2rem;
  }
}

.main {
  display: flex;
  height: 100%;
  background-color: #0e285d;
  position: relative;
}

.btn {
  display: block;
  padding: 1rem 2rem;
  margin: 2rem 1rem;
  margin: 1rem;
  color: #edf8ff;
  font-size: 1.2rem;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
}
.btn.rounded {
  border-radius: 2rem;
}
.btn-nav {
  text-align: left;
}
.btn-nav [class^=icon-], .btn-nav [class*=" icon-"] {
  margin-right: 1rem;
}

.card {
  padding: 2rem 1rem;
  border: #CEAA24 solid 2px;
  border-radius: 2rem;
  box-shadow: 0px 0px 1rem #121212;
}
.card h2, .card h3, .card h4 {
  text-align: center;
}
.card h2 small, .card h3 small, .card h4 small {
  color: #CEAA24;
}
.card h2 a, .card h3 a, .card h4 a {
  color: #edf8ff;
}
.card h2 a:hover, .card h3 a:hover, .card h4 a:hover {
  color: #edf8ff;
  text-decoration: underline;
}
.card-box {
  width: calc(100vw - 6rem - 4px);
  max-width: 600px;
}
@media (min-width: 768px) {
  .card-box {
    width: calc(50vw - 5rem - 4px);
  }
}
@media (min-width: 1024px) {
  .card-box {
    width: 300px;
  }
}
.card .card-content {
  width: 100%;
}

.horizontal-grid {
  display: flex;
  gap: 2rem;
  padding: 1rem 2rem;
  position: relative;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
}
.horizontal-grid div {
  scroll-snap-align: center;
}
@media (min-width: 1440px) {
  .horizontal-grid {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: center;
  }
}

.box {
  position: relative;
}
.box .touch-indicator-horizontal {
  position: absolute;
  display: block;
  width: 10%;
  bottom: 0;
  left: 45%;
  animation-name: move-right;
  animation-duration: 1.5s;
  animation-timing-function: ease-in-out;
  animation-direction: alternate;
  animation-iteration-count: infinite;
}

@keyframes move-right {
  from {
    left: 50%;
  }
  to {
    left: 40%;
  }
}
.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}

.section {
  padding: 1rem 2rem;
}
.section-heading {
  text-align: center;
  padding-bottom: 0.5rem;
  padding-top: 1rem;
}
.section-description {
  width: 75%;
  max-width: 768px;
  margin: 0 auto;
}

.nav-menu {
  position: absolute;
  left: -256px;
  z-index: 1;
  transition: 0.5s;
  min-width: 256px;
  width: 256px;
  height: 100vh;
  background-color: #0e285d;
  border-right: #CEAA24 solid 2px;
  /* Main Nav Menu */
  /* Social Media Buttons */
}
.nav-menu .hamburger {
  position: absolute;
  left: 100%;
  top: 64px;
  margin: 0;
  background-color: #0e285d;
  border: #CEAA24 solid 2px;
}
.nav-menu .hamburger .hamburger-box .hamburger-inner, .nav-menu .hamburger .hamburger-box .hamburger-inner::after, .nav-menu .hamburger .hamburger-box .hamburger-inner::before {
  background-color: #edf8ff;
}
@media (min-width: 1024px) {
  .nav-menu .hamburger {
    display: none;
  }
}
.nav-menu.is-active {
  left: 0;
}
.nav-menu .nav-wrapper {
  overflow: auto;
  height: 100vh;
}
@media (min-width: 1024px) {
  .nav-menu {
    position: relative;
    left: 0;
  }
}
.nav-menu .icon {
  padding: 0 1rem;
}
.nav-menu .icon a {
  display: block;
  width: 128px;
  height: 128px;
  margin: auto;
}
@media (min-width: 360px) and (max-width: 768px) {
  .nav-menu .icon a {
    width: 64px;
    height: 64px;
  }
}
.nav-menu nav {
  overflow-y: auto;
}
.nav-menu nav ul {
  padding: 0;
}
.nav-menu nav ul li {
  list-style: none;
  margin: 0;
  width: 100%;
}
.nav-menu nav ul li a {
  width: 100%;
  margin: 0;
}
.nav-menu .nav-social-media {
  text-align: center;
  margin: 0;
  padding: 1rem;
}
.nav-menu .nav-social-media li {
  display: inline;
}
.nav-menu .nav-social-media li a {
  display: inline-block;
  width: auto;
  background-color: #205AD5;
  color: #edf8ff;
  text-decoration: none;
  text-align: center;
  font-size: 1rem;
  line-height: 1rem;
  padding: 1rem;
  border-radius: 2rem;
}
.nav-menu .nav-social-media li a:hover {
  background-color: #63ADF7;
}
.nav-menu .nav-social-media li a span {
  margin: 0;
}

.grid {
  display: grid;
  justify-content: center;
  gap: 2rem;
  padding: 1rem 2rem 2rem;
}
@media (min-width: 768px) {
  .grid.col-2 {
    grid-template-columns: repeat(2, auto);
  }
}
@media (min-width: 768px) {
  .grid.col-3 {
    grid-template-columns: repeat(2, auto);
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .grid.col-3 {
    grid-template-columns: repeat(3, auto);
  }
}
@media (min-width: 768px) {
  .grid.col-4 {
    grid-template-columns: repeat(2, auto);
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .grid.col-4 {
    grid-template-columns: repeat(4, auto);
  }
}

.theme-primary {
  background-color: #0e285d;
